<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="false" :showCheckBox="true" :listType="[DetailListType]" ModuleName="Approval"
            SubModuleCode="Approval" IdentityColumn="GROUPID" :HeaderText="$t('TimesheetApprovalsHistory')"
            :ListData="GroupData" :HeaderData="Headers" :widgets="widgets" :callbackfunction="FetchData"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
            :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData" :LegendArray="LegendArray"
            :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:ActionButtonClick="actionButtonClick"
            v-on:SortdataButtonClick="sortdata">
        </tg-list>
    </section>
</template>

<script>
//import DataService from '../../service/DataService';
import Schema from '../../views/TimesheetApproval/TimesheetApprovalHistorySchema.js'

export default {

    data() {
        return {
            isLoading: false,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            LegendArray: [],
            Headers: [{
                "COLUMN_NAME": '',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('TimesheetPeriod'),
                "DISPLAY_ORDER": 1,

            },
            {
                "COLUMN_NAME": '',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('UserName'),
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": '',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('TrackedHour'),
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": '',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('PerDiem'),
                "DISPLAY_ORDER": 4,
            },
            {
                "COLUMN_NAME": '',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Status'),
                "DISPLAY_ORDER": 4,
                settings: {
                    isInSlot: true,
                }
            },
            {
                "COLUMN_NAME": '',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Comment'),
                "DISPLAY_ORDER": 4,
                settings: {
                    isInSlot: true,
                }
            },
            {
                "COLUMN_NAME": '',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Rollback'),
                "DISPLAY_ORDER": 4,
                settings: {
                    isInSlot: true,
                }
            },

            ],
            noRecord: true,
            SortBy: '',
            SortExp: '',
            GroupData: [],
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            CurrentPage: 1,
            TotalPages: 0,
            listheaderbuttons: Schema.buttons,
            DetailListType: 'List',
        }
    },
    created() {
        var vm = this;
        vm.FetchData();
        vm.LegendArray = [{
            class: 'legend-pending colorboxsquare',
            text: ('The approved entries which have been rejected(Override) by CA')
        },
        {
            class: 'legends_medium colorboxsquare',
            text: ('The pending entries which have been approved/rejected directly by CA')
        },]
    },
    methods: {
        async FetchData() {
            var vm = this;

        },
        BackTolist() {
            this.$router.push({
                name: 'Timesheet Approvals'
            });
        },
        actionButtonClick(callbackfunction, item) { },
        renderActions(row) { },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },

        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "BackTolist":
                    this.BackTolist();
                    break;
            }
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            } else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
    },

}
</script>
